.service-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 -5px; /* Adjust for the margin of the squares */
}

.service-square {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #ccc;
  padding: 20px;
  margin: 5px;
  background-color: var(--primary-color);
  color: var(--text-color);
  flex: 1 1 calc(33.333% - 10px); /* 3 columns layout */
  box-sizing: border-box;
  text-align: center;
}

.service-square h4 {
  margin: 0;
}
